/* ResultPage.module.css */

.resultContainer { 
    text-align: center;
    padding: 20px;
    display: flex;
    flex-flow: column nowrap;
}

.validCertificate,
.invalidCertificate {
    border: 2px solid #cccccc;
    padding: 20px;
    margin: 20px auto;
    border-radius: 10px;
    display: inline-block;
    max-width: 300px;
}

.validCertificate {
    border-color: #4CAF50;
}

.invalidCertificate {
    border-color: #F44336;
}

.validText,
.invalidText {
    font-size: 1.5em;
    margin-bottom: 15px;
}

.validText {
    color: #4CAF50;
}

.invalidText {
    color: #F44336;
}

.validIcon,
.invalidIcon {
    font-size: 2em;
    display: inline-block;
    margin-bottom: 15px;
}

.validIcon {
    color: #4CAF50;
}

.invalidIcon {
    color: #F44336;
}

.downloadButton {
    background-color: #4CAF50;
    color: white;
    padding: 10px 15px;
    border: none;
    border-radius: 5px;
    margin: 10px 0;
    cursor: pointer;
}

.goBackButton {
    background-color: #4a4a4a; /* Changed to a neutral dark gray */
    color: #ffffff; /* White text for contrast */
    padding: 10px 15px;
    font-size: 1em;
    border: none;
    border-radius: 5px;
    margin: 10px 0;
    height: 40px;
    cursor: pointer;
    align-self: center;
    width: 200px;
    transition: background-color 0.3s ease; /* Smooth transition for hover effect */
    
    &:hover{
        background-color: #6f42c1; /* A pleasant purple for the hover state */
        color: #ffffff; /* Keep text white for readability */
        cursor: pointer;
    }
}

/* Add additional styling as needed */