/* src/components/CertificateForm.module.css */

.formContainer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    padding: 20px;
    box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2); /* Optional: Adds a subtle shadow around the form */
    border-radius: 4px; /* Optional: Rounds the corners of the form */
    background-color: white; /* Optional: Sets a white background for the form */
    width:calc(80%);
    max-width: 500px; /* Optional: Max width for the form */
    margin: auto; /* Centers the form in the page */
}
  
.textField {
    margin-bottom: 16px;
    width: 100%; /* Makes the text fields full width within the form container */
}
  
.buttonContainer {
    display: flex;
    flex-direction: column;
    width: 100%;
    align-items: center;
    display: flex;

    justify-content: center;
    margin-top: 20px; /* Adjust this value as needed */
}

.buttonContainer .uploadButton {
    margin: 16px 0; width: 80%; background-color: #f0f0f0;  color: #333; 
    border: 1px dashed #666;  padding: 10px 0; display: flex; justify-content: center;
    align-items: center; font-size:13px;
    &:hover{color: white;}
}

.buttonContainer .uploadInput {
    display: none; /* Hide the actual file input */
}

.buttonContainer .verifyButton {
    padding: 15px; margin: 16px 0; width: 80%; background-color: #1976d2; cursor: pointer;
}
  

/*-- File Info Styles --*/
.fileInfo {
    text-align: center; color:black; margin-left:4px;
}

.fileDisplay {
    display: inline-flex; align-items: center; background-color: #f0f0f0;
    padding: 5px; border-radius: 4px; font-size:0.9em;
}

.fileDisplay span {
    margin-right: 10px; 
}

.fileDisplay .fileRemoveButton {
    background-color: #ff4d4d;  color: white;
    &:hover { background-color: #ff3333;}
}