/* AlertDialog.module.css */
.alert-dialog-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.5); /* Semi-transparent background */
    display: flex;
    justify-content: center;
    align-items: center;
    z-index: 999; /* Ensure it appears on top of other elements */
  }
  
  .alert-dialog {
    background: #fff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    max-width: 400px;
    width: calc(100% - 80px);
    text-align: center;
  }
  
  .alert-message {
    font-size: 16px;
    color:rgba(0, 0, 0, 0.8);
    margin-bottom: 20px;
  }
  
  .ok-button {
    background: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .ok-button:hover {
    background: #0056b3;
  }
  